@use '../base/variables' as v;
@use '../base/mixins' as m;

.iconos {
    margin-top: 5rem;

    .listado {

        @include m.tablet{
            @include m.grid(3, 2rem);
        }

        li {

            img{
                margin: 0 auto;
            }
            p {
                font-family: v.$cursiva;
                font-size: 3.2rem;
                color: v.$primario;
            }
        }
    }
}