@use '../base/variables' as v;
@use '../base/mixins' as m;

.menu {
    background-image: url(/img/menu_arriba.svg), url(/img/menu_abajo.svg), linear-gradient(to top, v.$primario, v.$primario);
    background-repeat: no-repeat;
    background-position: center 5rem, center 92%, 100%;
    background-size: 200%;
    padding: 15rem 5rem 20rem 5rem;

    @include m.tablet {
        padding: 15rem 0 20rem 0;
        background-size: 100%;
    }

    @include m.desktop {
        background-size: 50%, 50%, 100%;

    }

}

.grid-menu {
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
}

.menu {
    h3 {
        color: v.$blanco;
    }

    li {
        display: flex;
        justify-content: space-between;
        align-items: center;

        p {
            color: v.$secundario;
            font-size: 2.2rem;
            line-height: 1.5;
            margin: 0 3rem 2rem 0;
            font-weight: 700;

            span {
                display: block;
                color: v.$claro;
                font-weight: 400;
            }
        }

        .precio {
            font-size: 4rem;
            font-weight: 700;
            color: v.$blanco;

        }
    }

}